@import 'modules/__mixins.scss';
@import 'modules/__typography.scss';

$yo: yellow;

body {
    background: $yo;
    width: 100vw;
    height: 100vh;
}
h1 {
    @include h1();
}
p {
    @include p();
}
.v-center {
    @include vertical-center();
    & .t-left {
        display: inline-block;
        text-align: left;
    }
}